@mixin transition-opacity($delay, $delayed: false) {
    transition: opacity $delay;

    @if $delayed {
        transition-delay: $delay;
    }
}

@mixin radial-background($from, $to) {
    background: radial-gradient(circle, $from 50%, $to 150%);
}

body {
    #root {
        #app {
            #content {
                #landing {
                    @include radial-background(var(--blue), var(--white));

                    .main {
                        .container {
                            span {
                                font-family: "Nunito", sans-serif;
                                font-weight: 300;
                                text-transform: uppercase;

                                h1 {
                                    font-size: 3.75rem;
                                    text-align: center;
                                    color: var(--white);
                                }

                                h2 {
                                    text-align: center;
                                    color: var(--white);

                                    &.subTitle {
                                        font-size: 1.75rem;
                                        font-weight: 300;
                                    }
                                }
                            }

                            #field {
                                display: block;
                                margin: auto;
                                display: inline-block;
                                position: relative;
                                width: 100%;
                                overflow: hidden; 
                                padding: 2em;
                            }
                        }
                    }
                }
            }
        }
    }
}
